.buttons {
  padding: '5px';
  justify-content: center;
  text-align: center;

  .addtoCartButton {
    font-size: small;
  }
}

.cards {
  .catAndPrice {
    display: flex;
    margin-top: 10px;

    p {
      color: rgb(111, 111, 111)
    }
  }

  .catAndPrice>* {
    flex: 1;
  }
}

.cards,
.cardActionArea,
.cardMedias {
  transition: all 1s ease-out;
  justify-content: center;
}

.cards:hover {
  transition: all 1s ease-out;
  box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.147);

  .cardActionArea {
    transition: all 0.5s ease-out;
    transform: scale(1.05);
  }

  // .cardMedias {
  //   transition: all 0.5s ease-out;
  //   transform: scale(1.05);
  // }
}

