.profile_page {
    justify-content: center;

    .profile_page_container {
        margin: auto;
        margin-top: 80px;
        width: 90%;
        padding: 20px 40px;

        .goBack,
        .heading_container {
            margin-left: 30px;
        }

        .content_section {
            display: flex;
            padding: 20px;

            .left {
                flex: 0.5;
                text-align: center;
                padding: 20px;
                box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
                margin-right: 50px;

                .user_card {
                    margin: 20px;
                    padding: 20px;

                    img {
                        max-width: 350px;
                        padding: 20px;
                    }

                    p {
                        font-size: 10px;
                    }
                }

                .user_actions {
                    margin: 20px;
                    padding: 20px;
                    justify-content: center;
                    text-align: center;
                }
                .user_actions>* {
                    flex: 1 1 150px;
                    margin: 0px 10px;
                }
            }

            .right {
                flex: 1;
                background-color: rgba(209, 209, 209, 0.492);
                box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);

                .user_info {
                    margin: 10px 20px;
                    padding: 20px;


                    .row {
                        padding: 20px;
                        display: flex;

                        h2 {
                            font-size: 25px;
                            border-bottom: 1px solid #000;
                        }

                        .row_container {
                            justify-content: center;
                            margin-top: 50px;
                        }

                        .row-content {
                            flex: 1;
                        }

                        // label,
                        // input {
                        //     margin-top: 20px;
                        //     margin-left: 20px;
                        //     font-size: 20px;
                        // }

                        // input {
                        //     border: none;
                        //     outline: none;
                        //     background-color: white;
                        //     box-sizing: border-box;
                        //     box-shadow: inset 0 0 5px 2px rgb(239, 239, 239);
                        //     text-align: center;
                        // }

                        .button {
                            flex: 1;
                            padding: 10px;
                        }
                    }
                }
            }
        }
    }
}